@use "../abstract" as *;

.mail-content{
    margin-top: 60px;
    padding-top: 20px;
    .mail{
        padding: 36px 30px 30px;
        height: calc(100vh - 80px);
        section{
            @extend %clearfix;
            position: relative;
        }
    }
    .user{
        @include size(54px,54px,50%);
        position: relative;
        background-color: peachpuff;
        float: left;
        .icon{
            position: absolute;
            top: 0;
            right: 0;
            margin: 0;
        }
    }
    .title{
        font-size: 18px;
        color: $mail-title-clr;
    }

    .from{
        font-size: 13px;
        color: $text-clr-primary;
    }
    .sender{
        display: inline-block;
        @include size(93px,23px);
        padding: 5px 10px 3px 12px;
        border-radius: 12px;
        box-shadow: 0 0 0 1px $shadow-clr;
        font-size: 12px;
        color: $mail-sender-clr-secondary;
        cursor: pointer;
        i{
            margin-left: 3px;
            font-size: 8px;
        }
        &:hover{
            background-color: $shadow-clr;
        }
    }
    .mail-description{
        position: absolute;
        background-color: $white;
        padding: 10px;
        left: 120px;
        z-index: 10;
        border: 1px solid $border-clr-primary;
        font-size: 12px;
        color: $mail-sender-clr-secondary;
        border-radius: 5px;
        table{
            width:300px;
            
            td{
                padding: 5px;
                span{
                    display: inline-block;
                    overflow: hidden;
                }
                &:first-child{
                    width: 100px;
                    text-align: right;
                }
                &:last-child{
                    span{
                        width: 200px;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }                
                }
            }
        }
    }

    .icon-nav{
        float: right;
        @include size(170px,34px,18px);
        box-shadow: 0 0 0 1px $shadow-clr;
        @extend %clearfix;
        button{
            width: 25%;
            height: 100%;
            float: left;
            color: $btn-clr-tertiary;
            font-size: 16px;
            margin-right: 0;
            border-right: 1px solid $border-clr-primary;
            &:hover{
                background-color: $shadow-clr;
            }
            &:first-child{
                border-radius: 18px 0 0 18px;
            }
            &:last-child{
                border-radius: 0 18px 18px 0;
                border: none;
            }
        }
        button.active{
            color: $white;
            background-color: $btn-clr-primary;
            &:hover{
                background-color: darken($btn-clr-primary,12%);
            }
        }
    }

    hr{
        background-color: $thematic-break;
        padding: 0; 
        border:none;
        @include size(100%,2px);
        clear: both;
    }
    .content{
        height: 80%;
        font-size: 14px;
        color: $mail-text-color;
        overflow: hidden;
        position: relative;
        // .overlay{
        //     height: 100%;
        //     position: relative;
        //     &::after{
        //         content: "";
        //         position: absolute;
        //         bottom: 0;
        //         left: 0;
        //         width: 100%;
        //         height: 100%;
        //         background-image: linear-gradient(transparent 80%, white);
        //     }
        // }
        textarea{
            width: 100%;
            height: 100%;
            max-height: 570px;
            padding: 0 10px 0 0;
            font-size: 14px;
            color: $mail-text-color;
            border:none;
            resize: none;
            &:focus{
                outline:none;
            }
            overflow-y: scroll;
            scrollbar-width:thin;
            scrollbar-color: $scrollbar-clr  white;
            &::-webkit-scrollbar{
                width: 8px;
                background-color: white;
            }
            &::-webkit-scroll-track{
                border-radius: 10px;
            }
            &::-webkit-scrollbar-thumb{
                border-radius: 10px;
                background-color: $scrollbar-clr;
            }
        }
    }
}